$font-family: "Avenir Next LT Pro", sans-serif
$second-family: "Gilroy", sans-serif
html
    position: relative
    overflow-x: hidden
body
    font-family: $font-family
    position: relative
    background: #0c131d
    background-image: url('../img/star_bg.svg')
    background-position: center
    background-repeat: repeat
    background-size: contain
    min-height: 100vh
    overflow: hidden
.container
    max-width: 1110px
    padding: 0 20px
    margin: 0 auto
    box-sizing: content-box
.btn
    &__blue
        border: 1.50px solid rgba(255, 255, 255, 0.1)
        border-radius: 50px
        backdrop-filter: blur(20px)
        background: linear-gradient( rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%)
        padding: 20px 30px
        font: 500 20px $font-family
        text-align: center
        color: #fff
        width: max-content
        z-index: 2
        border: none
        box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 1px 0 0 1px rgba(255, 255, 255, 0.3)
        position: relative
        overflow: hidden
        transition: all .3s
        display: grid
        grid-template-columns: 1fr 16px
        gap: 11px
        align-items: center
        &::after
            content: ''
            display: block
            width: 16px
            height: 16px
            background: url('../img/arrow.svg') center no-repeat
            background-size: contain
            transition: all .3s
            opacity: 0
        &::before
            border-radius: 50px
            content: ''
            display: block
            position: absolute
            top: 0%
            left: 0%
            width: 100%
            height: 100%
            transform: scale(0) 
            background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
            z-index: -1
            transition: all .3s
        &:hover
            color: #000
            &::after
                opacity: 1
                filter: invert(1)
            &::before
                transform: scale(1)
    &__wht
        border-radius: 50px
        padding: 20px 30px
        border: none
        background: linear-gradient(90deg, #87aedb 0%, #c9e1ff 100%)
        font: 500 20px $font-family
        color: #000
        width: max-content
        z-index: 2
section
    padding: 80px 0 0
    z-index: 1
    position: relative
@keyframes motionAnim
    0%
        left: 0
    50%
        left: 100%
    100%
        left: 0
@keyframes starTail
    0%
        transform: scale(1) rotate(-45deg) translate(0px, 0px)
    15%
        transform: scale(0.5) rotate(-45deg) translate(0px, -1px)
@keyframes starFall
    0%
        opacity: 0
        transform: scale(0) translate3d(0px, 0px, 0px)
    7.5%
        opacity: 1
        transform: scale(1) translate3d(-200px, 200px, 0px)
    22.5%
        opacity: 0
        transform: scale(1) translate3d(-300px, 300px, 0px)
@keyframes moveAlongPath 
    0% 
        opacity: 0
        right: 0
        top: 20%
    20% 
        opacity: 1
        right: 14%
        top: 74%
    25% 
        right: 19%
        top: 85%
    30% 
        right: 25%
        top: 93%
    70% 
        right: 67%
        top: 84%
    80% 
        right: 79%
        top: 65%
    92% 
        right: 91%
        top: 37%
        opacity: 1
    to 
        opacity: 0
        right: 96%
        top: 14%
@keyframes stepsWallet 
    0% 
        left: 15px
        opacity: 0
        transform: translate(-50%, -50%) scale(0)
    50% 
        opacity: 1
        transform: translate(-50%, -50%) scale(1)
    to
        left: calc(100% - 15px)
        opacity: 0
        transform: translate(-50%, -50%) scale(0)
@keyframes stepsArrow1 
    0% 
        transform: scale(0)
    50% 
        opacity: 1
    to 
        transform: scale(1)
        opacity: 0
@keyframes whyAnim 
    0%
        top: 0
        right: 50%
    40%
        right: 50px
        top: 0
    43%
        right: 0px
        top: 50px
    50%
        right: 0
        top: calc( 100% - 50px )
    53%
        right: 50px
        top: 100%
    100%    
        right: 50%
        top: 100%
@keyframes parArrow
    0%
        transform: rotate(30deg) translate(-40px, 10px)
        opacity: 0
    50%
        transform: rotate(0deg) translate(-20px, 50px)
        opacity: 1
    100%
        transform: rotate(-30deg) translate(-20px, 90px)
        opacity: 0

